////
/// Components/Puzzle
/// State
////

///
/// Page load
.Puzzle--idHero {
  transition: opacity get-duration(slow) get-easing();
}

.Section--isLoading {
  .Puzzle--idHero {
    opacity: 0;
  }
}

///
/// Shadow tuck-in
.Shadow--typeCube {
  .draggable-source--is-dragging & {
    opacity: 0;
    transform: translateY(-(get-spacing()));
  }

  .CollidableCube8 &,
  .CollidableCube10 & {
    display: none;
  }
}

///
/// Common dragging
.draggable--is-dragging {
  &,
  .draggable-source,
  .draggable-mirror {
    cursor: get-cursor(drag, active);
  }

  &,
  > * {
    user-select: none;
  }
}

///
/// Droppable
.draggable-droppable--occupied {
  .Cube {
    transform: none;
  }
}

///
/// Cube Source
.draggable-source {
  cursor: get-cursor(drag);
}

.draggable-source--placed {
  .JellyInterior {
    @include jelly-animation-reverse;
  }
}

.draggable-source--is-dragging {
  &.FaceWrapper {
    .Face {
      overflow: hidden;

      &::before {
        @include position-cover;
        content: '';
        display: block;
        border-radius: 50%;
        background-color: rgba(white, 0.08);
        box-shadow: 0 0 1.2rem 0.6rem rgba(white, 0.08);
        animation: pulse get-duration(slower) get-easing() infinite both;
      }

      .Svg {
        opacity: 0;
      }
    }
  }
}

///
/// Cube Mirror
.draggable-mirror {
  z-index: get-z-index(surface);

  &.Cube {
    transform: none;
  }

  .JellyInterior {
    @include jelly-animation;
  }

  &:not(.Cube--typeSolo) {
    .FacePop {
      @include pop-animation;
    }

    .Face {
      border-radius: get-radius(tight);
      box-shadow: inset 0 0 0 0.2rem black, 0 0 0 0.2rem black;

      @media screen and (min-width: get-breakpoint(tablet)) {
        box-shadow: inset 0 0 0 0.3rem black, 0 0 0 0.3rem black;
      }

      @media screen and (min-width: get-breakpoint(tablet, wide)) {
        box-shadow: inset 0 0 0 0.5rem black, 0 0 0 0.5rem black;
      }
    }
  }

  .Shadow--typeCube {
    // for whatever reason... a transition here just would not work
    display: none;
  }
}

///
/// Collisions
.Cube.isColliding .Jelly {
  animation: shake get-duration() get-easing();
}
