////
/// Components
/// Error404
////

// Import `completed` Brand and Stars mixin
@import '../Brand/mixins';
@import '../Stars/mixins';

#Page404 {
  height: 100vh;

  main {
    height: 100%;
  }

  @media screen and (min-width: get-breakpoint(tablet)) {
    padding: 0;
  }
}

.Error404 {
  @include stars-parent-section;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background-color: black;

  ::selection {
    color: black;
    background-color: get-color(brand, yellow);
  }

  .Content {
    margin-bottom: 0;
  }

  ///
  /// Logo override
  .Wave--colorPurple {
    fill: get-color(brand, purple);
  }

  .Wave--colorRed {
    fill: get-color(brand, red);
  }

  .Wave--colorOrange {
    fill: get-color(brand, orange);
  }

  .Brand:hover {
    @include logo-404;
  }
}
