////
/// Components
/// Notification
////

@import './keyframes';

$notification-icon-size: 3.2rem;

.Notification {
  @include centered-width(get-layout-width());
  position: fixed;
  z-index: get-z-index(overlay);
  right: get-spacing(tighter);
  bottom: get-spacing(tighter);
  left: get-spacing(tighter);
  display: block;
  border-radius: get-radius(loose);
  text-align: left;
  box-shadow: get-shadow(subtle);
  background-color: get-color(brand, blue);
  transition: background-color get-duration() get-easing();
  animation: notification-enter get-duration(slow) get-easing(bungie) both;

  &:focus {
    // reapply style because of reset
    box-shadow: get-shadow(subtle);
  }

  &:focus,
  &:hover {
    background-color: darken(get-color(brand, blue), 4%);

    .NotificationIcon {
      transform: translate(0.2rem, -0.2rem);
    }
  }

  &:active {
    background-color: get-color(brand, blue);

    .NotificationIcon {
      transform: translate(0, 0);
    }
  }
}

.NotificationInterior {
  display: flex;
  align-items: center;
  padding: get-spacing(tighter);
}

.NotificationIcon {
  flex: 0 0 auto;
  width: $notification-icon-size;
  margin-right: get-spacing(tighter);
  transition: transform get-duration() get-easing();

  .Svg {
    fill: get-color(brand, yellow);
  }
}

.NotificationMessage {
  flex: 1 1 auto;
  font-size: get-type-scale(paragraph);
  font-weight: 600;
  line-height: 1.3;
  color: white;
  user-select: none;
  // reset the cursor set for typography
  cursor: get-cursor(pointer);
}
