////
/// Components
/// Brand keyframes
////

@keyframes logo-rainbow-mask {
  0% {
    fill: get-color(brand, blue);
  }

  25% {
    fill: get-color(brand-muted, purple);
  }

  50% {
    fill: get-color(brand-muted, red);
  }

  75% {
    fill: get-color(brand-muted, orange);
  }
}

@keyframes logo-rainbow-purple {
  0% {
    fill: get-color(brand-muted, purple);
  }

  25% {
    fill: get-color(brand-muted, red);
  }

  50% {
    fill: get-color(brand-muted, orange);
  }

  75% {
    fill: get-color(brand, blue);
  }
}

@keyframes logo-rainbow-red {
  0% {
    fill: get-color(brand-muted, red);
  }

  25% {
    fill: get-color(brand-muted, orange);
  }

  50% {
    fill: get-color(brand, blue);
  }

  75% {
    fill: get-color(brand-muted, purple);
  }
}

@keyframes logo-rainbow-orange {
  0% {
    fill: get-color(brand-muted, orange);
  }

  25% {
    fill: get-color(brand, blue);
  }

  50% {
    fill: get-color(brand-muted, purple);
  }

  75% {
    fill: get-color(brand-muted, red);
  }
}

@keyframes logo-outline-bounce {
  25% {
    transform: translate(-0.1rem, 0.1rem);
  }

  75% {
    transform: translate(0.1rem, -0.1rem);
  }
}

