////
/// Components
/// Brand mixins
////

@mixin logo-default {
  .Wave--colorMask {
    animation: logo-rainbow-mask get-duration(slow) get-easing() infinite;
  }

  .Wave--colorPurple {
    animation: logo-rainbow-purple get-duration(slow) get-easing() infinite;
  }

  .Wave--colorRed {
    animation: logo-rainbow-red get-duration(slow) get-easing() infinite;
  }

  .Wave--colorOrange {
    animation: logo-rainbow-orange get-duration(slow) get-easing() infinite;
  }

  .Hand {
    animation: logo-outline-bounce get-duration(slow) linear infinite;
  }
}

@mixin logo-completed {
  .Wave--colorMask {
    animation-name: logo-rainbow-mask-completed;
  }

  .Wave--colorPurple {
    animation-name: logo-rainbow-purple-completed;
  }

  .Wave--colorRed {
    animation-name: logo-rainbow-red-completed;
  }

  .Wave--colorOrange {
    animation-name: logo-rainbow-orange-completed;
  }
}

@mixin logo-404 {
  .Wave--colorMask {
    animation-name: logo-rainbow-mask-404;
  }

  .Wave--colorPurple {
    animation-name: logo-rainbow-purple-404;
  }

  .Wave--colorRed {
    animation-name: logo-rainbow-red-404;
  }

  .Wave--colorOrange {
    animation-name: logo-rainbow-orange-404;
  }
}
