////
/// Components
/// Features
////

.Features {
  padding-top: get-spacing(fattest);
  padding-bottom: get-spacing();
  background-color: get-color(purple);

  @media screen and (min-width: get-breakpoint(tablet)) {
    padding-top: get-spacing(huger);
    padding-bottom: get-spacing(fat);
  }

  @media screen and (min-width: get-breakpoint(tablet, wide)) {
    padding-top: get-spacing(jumboest);
    padding-bottom: get-spacing(fatter);
  }

  ::selection {
    color: get-color(purple);
  }
}
