////
/// Components
/// Brand
////

@import './keyframes';
@import './mixins';

$logo-draggable-width: 4.6rem;
$logo-draggable-width-tablet: 6rem;
$wordmark-draggable-width: 23.4rem;
$wordmark-draggable-width-tablet: 32rem;

.Brand {
  width: $wordmark-draggable-width;
  margin-right: auto;
  margin-bottom: get-spacing(tight);
  margin-left: auto;
  cursor: get-cursor(rock);

  @media screen and (min-width: get-breakpoint(tablet)) {
    width: $wordmark-draggable-width-tablet;
  }

  &:active {
    cursor: get-cursor(rock, active);
  }
}

.BrandLogo {
  width: $logo-draggable-width;
  margin-right: auto;
  margin-left: auto;

  @media screen and (min-width: get-breakpoint(tablet)) {
    width: $logo-draggable-width-tablet;
  }
}

.BrandWordmark {
  .Svg {
    fill: get-color(brand, yellow);
  }
}

///
/// Logo
.Wave--colorMask {
  // transparent so it doesn't appear to instantly switch colors
  // when changing to the `completed` state
  fill: transparent;
}

.Wave--colorPurple {
  fill: get-color(brand-muted, purple);
}

.Wave--colorRed {
  fill: get-color(brand-muted, red);
}

.Wave--colorOrange {
  fill: get-color(brand-muted, orange);
}

.Hand {
  fill: get-color(brand, yellow);
}

// --- Interaction
.Brand {
  &:hover {
    @include logo-default;
  }

  &:active {
    .Wave--colorMask,
    .Wave--colorPurple,
    .Wave--colorRed,
    .Wave--colorOrange,
    .Hand {
      animation-duration: get-duration();
    }
  }
}
