///
/// Fully completed puzzle

// --- Secret key
.Puzzle--idHero {
  .Face--isBlank .Svg {
    opacity: 0;
    fill: none;
    stroke: black;
    // chrome on windows does not seem to give weight to the stroke?
    // perhaps this bit of css will help
    stroke-width: $secret-stroke-width;
  }

  // Vier
  .Cube:nth-child(3) .Face--isBlank .Svg {
    stroke-dasharray: $secret-key-length-vier;
    stroke-dashoffset: $secret-key-length-vier;
  }

  // Agt
  .Cube:nth-child(19) .Face--isBlank .Svg {
    stroke-dasharray: $secret-ley-length-agt;
    stroke-dashoffset: $secret-ley-length-agt;
  }

  // Drie
  .Cube:nth-child(27) .Face--isBlank .Svg {
    stroke-dasharray: $secret-key-length-drie;
    stroke-dashoffset: $secret-key-length-drie;
  }
}

// --- Completed styles
.Hero {
  transition: background-color get-duration(completed-puzzle, speed) get-duration(completed-puzzle, delay) get-easing();
}

.Hero--isCompleted {
  background-color: black;

  .draggable-source {
    cursor: get-cursor(rock);

    &:active {
      cursor: get-cursor(rock, active);
    }
  }

  /* stylelint-disable selector-max-class */
  &.Hero .Cube .Face .Svg {
    transition-duration: get-duration(completed-puzzle, speed);
    transition-delay: get-duration(completed-puzzle, delay);
    fill: black;
  }
  /* stylelint-enable selector-max-class */

  .Stars {
    animation: fade-in get-duration(completed-puzzle, speed) get-duration(completed-puzzle, delay) get-easing() both;
  }

  // --- Buttons within Hero
  .Button {
    @include Button--typeCosmic;
  }

  // --- Secret key
  // Vier
  .Cube:nth-child(3) .Face--isBlank .Svg {
    animation: fade-in get-duration() $secret-fade-delay-vier get-easing() both,
      svg-trace-stroke get-duration(slowest) $secret-fade-delay-vier get-easing() both;
  }

  // Agt
  .Cube:nth-child(19) .Face--isBlank .Svg {
    animation: fade-in get-duration() $secret-fade-delay-agt get-easing() both,
      svg-trace-stroke get-duration(slowest) $secret-fade-delay-agt get-easing() both;
  }

  // Drie
  .Cube:nth-child(27) .Face--isBlank .Svg {
    animation: fade-in get-duration() $secret-fade-delay-drie get-easing() both,
      svg-trace-stroke get-duration(slowest) $secret-fade-delay-drie get-easing() both;
  }

  /// --- Prevent :hover styles on theme
  .Theme-Hero {
    .draggable-source:hover .Face--typeLeft {
      background-color: get-color(brand-muted, blue-lighter);
    }

    .draggable-source:hover .Face--typeRight {
      background-color: get-color(brand-muted, blue-light);
    }

    .draggable-source:hover .Face--typeTop {
      background-color: get-color(purple, light);
    }
  }

  // --- Completed Logo Animation
  .Brand:hover {
    @include logo-completed;
  }
}
